import React, {Component} from 'react';
import './App.css';
import {Link } from "react-router-dom";
import Song from './songs/Seek the Kingdom/Intro by Justin Townsend.mp4';
import Song2 from './songs/Seek the Kingdom/Glory to God (Is. 601) by Justin Townsend.mp4';
import Song3 from './songs/Seek the Kingdom/Seek the Kingdom (Matt. 633).mp4';
import Song4 from './songs/Seek the Kingdom/Anxiety (Is. 4110).mp4';
import Song5 from './songs/Seek the Kingdom/I Am With You (Deut. 318).mp4';
import Song6 from './songs/Seek the Kingdom/Worn (Matt. 1128).mp4';
import Song7 from './songs/Seek the Kingdom/We Are the Light (Matt. 516).mp4';
import Song8 from './songs/Seek the Kingdom/Rejoice (Phil. 44).mp4';
import Song9 from './songs/Seek the Kingdom/The Joy of the Lord (Ne. 810).mp4';
import Song10 from './songs/Seek the Kingdom/God Has Never Left You (Heb.135).mp4';
import Song11 from './songs/Seek the Kingdom/Outro.mp4';
import Background from './background.jpg';

class Seek extends Component{
  render(){
    return (
      <div>
		{/* Nav */}
    <nav className="navbar navbar-expand-lg left">
                <div className="container-fluid">
                    <Link to="/">
                        <h5 className="navbar-brand left">Home</h5>
                    </Link>
                    <button className="navbar-toggler" type="button" data-bs-toggle="collapse" data-bs-target="#navbarNav" aria-controls="navbarNav" aria-expanded="false" aria-label="Toggle navigation">
                        <span className="navbar-toggler-icon"></span>
                    </button>
                    <div className="collapse navbar-collapse" id="navbarNav">
                    <ul className="navbar-nav">
                        <li className="nav-item">
                        <Link to="/live">
                        <h5 className="nav-link active left" aria-current="page">Live in the Moment</h5>
                        </Link>
                        </li>
						<li className="nav-item">
                        <Link to="/seek">
                        <h5 className="nav-link active left" aria-current="page">Seek the Kingdom</h5>
                        </Link>
                        </li>
						<li className="nav-item">
                        <Link to="/unknown">
                        <h5 className="nav-link active left" aria-current="page">The Unknown</h5>
                        </Link>
                        </li>
						<li className="nav-item">
                        <Link to="/journey">
                        <h5 className="nav-link active left" aria-current="page">The Journey Begins</h5>
                        </Link>
                        </li> <li className="nav-item">
                        <Link to="/without">
                        <h5 className="nav-link active left" aria-current="page">Without You</h5>
                        </Link>
                        </li>
                    </ul>
                </div>
            </div>
        </nav>

    {/* Header */}
    <header id="header">
        <img src={Background} alt="BackgroundImage" />
        <h1 className="centered">Justin Townsend Official</h1>
        <h2 className="headtext">Seek the Kingdom</h2>
	</header>

	{/* About */}
		<div className="left">
			<h2>Seek the Kingdom</h2>
            <p>Seek the Kingdom was my second album I made. I came up with idea that with every song I make in this album, I'm going to put a Bible verse next to the title, and just maybe it would bring people to know Christ, and to take comfort in Him in their darkest time.</p>
		</div>

		{/* Main */}
			<div id="main">

				<section className="wrapper style1">
					<div className="inner">
						<header className="align-center">
							<h1>Intro</h1>
						</header>
						<div className="video">
							<div className="video-wrapper">
              <video controls src={Song}></video>
							</div>
						</div>
					</div>
				</section>

				<section className="wrapper style1 center">
					<div className="inner">
						<header className="align-center">
							<h1>Glory to God (Is. 60:1)</h1>
						</header>
						<div className="video">
							<div className="video-wrapper">
              <video controls src={Song2}></video>
							</div>
						</div>
					</div>
				</section>

				<section className="wrapper style1">
					<div className="inner">
						<header className="align-center">
							<h1>Seek the Kingdom (Matt. 6:33)</h1>
						</header>
						<div className="video">
							<div className="video-wrapper">
              <video controls src={Song3}></video>
							</div>
						</div>
					</div>
				</section>

				<section className="wrapper style1 center">
					<div className="inner">
						<header className="align-center">
							<h1>Anxiety (Is. 41:10)</h1>
						</header>
						<div className="video">
							<div className="video-wrapper">
              <video controls src={Song4}></video>
							</div>
						</div>
					</div>
				</section>

				<section className="wrapper style1">
					<div className="inner">
						<header className="align-center">
							<h1>I Am With You (Deut. 31:8)</h1>
						</header>
						<div className="video">
							<div className="video-wrapper">
              <video controls src={Song5}></video>
							</div>
						</div>
					</div>
				</section>

                <section className="wrapper style1">
					<div className="inner">
						<header className="align-center">
							<h1>Worn (Matt. 11:28)</h1>
						</header>
						<div className="video">
							<div className="video-wrapper">
              <video controls src={Song6}></video>
							</div>
						</div>
					</div>
				</section>

                <section className="wrapper style1">
					<div className="inner">
						<header className="align-center">
							<h1>We Are the Light (Matt. 5:16)</h1>
						</header>
						<div className="video">
							<div className="video-wrapper">
              <video controls src={Song7}></video>
							</div>
						</div>
					</div>
				</section>

                <section className="wrapper style1">
					<div className="inner">
						<header className="align-center">
							<h1>Rejoice (Phil. 4:4)</h1>
						</header>
						<div className="video">
							<div className="video-wrapper">
              <video controls src={Song8}></video>
							</div>
						</div>
					</div>
				</section>

                <section className="wrapper style1">
					<div className="inner">
						<header className="align-center">
							<h1>The Joy of the Lord (Ne. 8:10)</h1>
						</header>
						<div className="video">
							<div className="video-wrapper">
              <video controls src={Song9}>God Has Never Left You (Heb.13:5)</video>
							</div>
						</div>
					</div>
				</section>

                <section className="wrapper style1">
					<div className="inner">
						<header className="align-center">
							<h1>God Has Never Left You (Heb.13:5)</h1>
						</header>
						<div className="video">
							<div className="video-wrapper">
              <video controls src={Song10}></video>
							</div>
						</div>
					</div>
				</section>

                <section className="wrapper style1">
					<div className="inner">
						<header className="align-center">
							<h1>Outro</h1>
						</header>
						<div className="video">
							<div className="video-wrapper">
              <video controls src={Song11}></video>
							</div>
						</div>
					</div>
				</section>
			</div>

		{/* Footer */}
			<footer id="footer">
				<div className="copyright center">
					<ul className="icons">
						<li><a target="blank" href="https://www.youtube.com/channel/UCaDHVTA1pSE8pMsDaj1juSw">YouTube</a></li>
						<li><a target="blank" href="https://www.facebook.com/justin.townsend.7330763">Facebook</a></li>
						<li><a target="blank" href="https://www.instagram.com/im_justin_time/">Instagram</a></li>
					</ul>
					&copy; Untitled. Images: <a href="https://unsplash.com">Coverr</a>.
				</div>
			</footer>
      </div>
    );
  }
}

export default Seek;
